import React from "react";
import intl from "react-intl-universal";
import TextField from "components/TextField";
import Label from "components/Label";
import Columns from "components/Columns";
import Column from "components/Column";
import Button from "components/Button";
// import Select from "components/Select";
import withCrossingContext from "HOC/withCrossingContext";

const AlienFields = () => (
  <React.Fragment>
    <Columns>
      <Column style={{ borderRight: "1px solid #eee" }}>
        <Columns>
          <Column>
            <Label className="is-small">{intl.get("firstName")}</Label>
            FirstName
          </Column>
          <Column>
            <Label className="is-small">{intl.get("fatherName")}</Label>
            FatherName
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">
              {intl.get("grandfatherName")}
            </Label>
            GrandfatherName
          </Column>
          <Column>
            <Label className="is-small">{intl.get("familyName")}</Label>
            FamlyName
          </Column>
        </Columns>

        <Columns>
          <Column>
            <Label className="is-small">{intl.get("alienId")}</Label>
            Alien ID
          </Column>
          <Column>
            <Label className="is-small">{intl.get("alienIdVersion")}</Label>
            AlienIdVersion
          </Column>
          <Column>
            <Label className="is-small">{intl.get("alienIdStatus")}</Label>
            alienIdStatus
          </Column>
          <Column className="is-narrow">
            <Button className="is-small is-light">Detail</Button>
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">{intl.get("visaNumber")}</Label>
            VisaNumber
          </Column>
          <Column>
            <Label className="is-small">{intl.get("visaIssue")}</Label>
            VisaIssue
          </Column>
          <Column>
            <Label className="is-small">{intl.get("visaCountry")}</Label>
            VisaCountry
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">
              {intl.get("visaFinalReturnDate")}
            </Label>
            VisaFinalReturnDate
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("visaDepartureDate")}
            </Label>
            VisaDepartureDate
          </Column>
        </Columns>
      </Column>
      <Column>
        <Columns>
          <Column>
            <Label className="is-small">{intl.get("travelDate")}</Label>
            TravelDate
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("travelCountryCode")}
            </Label>
            TravelCountryCode
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("travelCountryName")}
            </Label>
            TravelCountryName
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">{intl.get("passportNumber")}</Label>
            PassportNumber
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("passportNationality")}
            </Label>
            PassportNationality
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">{intl.get("passportStatus")}</Label>
            PassportStatus
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("passportExpiryDate")}
            </Label>
            PassportExpiryDate
          </Column>
        </Columns>
        <Columns>
          <Column>
            <Label className="is-small">
              {intl.get("travelCarrierNumber")}
            </Label>
            TravelCarrierNumber
          </Column>
          <Column>
            <Label className="is-small">
              {intl.get("travelCarrierDescription")}
            </Label>
            TravelCarrierDescription
          </Column>
        </Columns>
      </Column>
    </Columns>
  </React.Fragment>
);

export default withCrossingContext(AlienFields);



