import React from "react";
import intl from "react-intl-universal";
import { routePaths } from "containers/Routes";
import {
  travelerTypes,
  crossingTypes,
  crossingActions
} from "contexts/crossing";
import compose from "utils/compose";
import withCrossingContext from "HOC/withCrossingContext";
import withSessionContext from "HOC/withSessionContext";
import withApiService from "HOC/withApiService";
import Link from "components/Link";
import Image from "components/Image";
import profileImage from "images/profile-img.png";
import Icon from "components/Icon";
import Table from "components/Table";
import Columns from "components/Columns";
import Column from "components/Column";
import Checkbox from "components/Checkbox";
import Button from "components/Button";
import Textarea from "components/Textarea";
import Form from "components/Form";
import Box from "components/Box";
import Modal from "components/Modal";
import CitizenFields from "containers/Crossing/SearchResult/CitizenFields";
import AlienFields from "containers/Crossing/SearchResult/AlienFields";
import VisitorFields from "containers/Crossing/SearchResult/VisitorFields";
import PageContainer from "components/PageContainer";
import "./SearchResult.css";
import Collapse from "../../../components/Collapse/Collapse";
import { makeCrossingUrlFor } from "HOC/withApiService";

const { ARRIVAL } = crossingTypes;
const { CITIZEN, PILGRIM, ALIEN, VISITOR } = travelerTypes;
const { COMPLETE, SEARCH, UPDATE } = crossingActions;

const dependentListHeaders = [
  "include",
  "id",
  "firstName",
  "fatherName",
  "grandfatherName",
  "familyName",
  "status"
];

const mockDependentList = [
  {
    include: false,
    personId: 1,
    firstName: "first-name",
    fatherName: "father-name",
    grandfatherName: "grandfather-name",
    familyName: "family-name",
    status: "status"
  },
  {
    include: false,
    personId: 2,
    firstName: "first-name",
    fatherName: "father-name",
    grandfatherName: "grandfather-name",
    familyName: "family-name",
    status: "status"
  },
  {
    include: false,
    personId: 3,
    firstName: "first-name",
    fatherName: "father-name",
    grandfatherName: "grandfather-name",
    familyName: "family-name",
    status: "status"
  }
];

class SearchResult extends React.Component {
  formEl = React.createRef();
  _setState = this.setState;

  constructor(props) {
    super(props);
    const { crossingType } = this.props.crossingContext;
    this.state = {
      isSkipBiometricVerification: false,
      title: "",
      currentState: "idle"
    };
  }

  componentDidMount() {
    this.props.crossingContext._setState({
      dependents: mockDependentList
    });
  }

  componentWillUnmount() {
    this._setState = null;
  }

  updateTripInfo = async () => {
    const {
      apiService: { post, urls },
      crossingContext: {
        personId,
        passportType,
        passportNumber,
        movements,
        selectedMovement
      },
      history,
      sessionContext
    } = this.props;
    // const { movements, selectedMovement } = this.state;
    // try {
    const movement =
      (movements.length > 0 && movements.filter(m => m.isSelected)[0]) || {};
    // if (isEditing in movement) delete movement.isEditing;
    // if ("id" in movement) delete movement.id;
    let putData = {
      expectedMovement: {
        ...movement,
        travelerId: personId,
        passportType,
        passportNumber
      }
    };
    // const res = await post({
    //   url: urls.crossing.updateTripInfo,
    //   method: "PUT",
    //   data: putData
    // });

    //todo: remove
    const res = {
      message: "success"
    };
    ///////////////////////////
    this._setState &&
      this._setState(
        {
          currentState: "success"
        },
        () => {
          setTimeout(() => {
            history.push(routePaths.crossing.search);
          }, 1000);
        }
      );
  };

  next = () => {
    if (!this.state.isSkipBiometricVerification) {
      this.props.history &&
        this.props.history.push(routePaths.crossing.fingerVerification);
      return;
    }
    const { sessionContext } = this.props;
    this._setState &&
      this._setState(
        {
          currentState: "loading"
        },
        async () => {
          try {
            const hasHitReports = await this.searchCWL();
            if (!hasHitReports) {
              await this.completeCrossing();
              await this.updateCrossing();
              await this.updateTripInfo();
            }
          } catch (e) {
            sessionContext.addNotification({ message: e });
          } finally {
            this._setState && this._setState({ currentState: "idle" });
          }
        }
      );
  };

  searchCWL = async () => {
    let hasHitReports = false;

    const {
      apiService: { post, urls },
      crossingContext,
      history
    } = this.props;

    // const res = await post({
    //   url: urls.crossing.searchCwl,
    //   data: {
    //     consolidatedWatchListBasicSearchForm: {
    //       travelerId: crossingContext.personId,
    //       fingerPosition: "string",
    //       fingerData: "string",
    //       sourceId: "string"
    //     }
    //   }
    // });

    let res = {
      borderHitWrappers: [
        {
          sourceId: Math.random().toString(),
          hitType: "miscreant",
          samisId: Math.random().toString(),
          hitAction: Math.random().toString(),
          name: Math.random().toString(),
          nationality: Math.random().toString(),
          birthDateH: Math.random().toString(),
          birthDateG: Math.random().toString()
        },
        {
          sourceId: Math.random().toString(),
          hitType: "wanted",
          samisId: Math.random().toString(),
          hitAction: Math.random().toString(),
          name: Math.random().toString(),
          nationality: Math.random().toString(),
          birthDateH: Math.random().toString(),
          birthDateG: Math.random().toString()
        },
        {
          sourceId: Math.random().toString(),
          hitType: "afis",
          samisId: Math.random().toString(),
          hitAction: Math.random().toString(),
          name: Math.random().toString(),
          nationality: Math.random().toString(),
          birthDateH: Math.random().toString(),
          birthDateG: Math.random().toString()
        }
      ]
    };

    return new Promise(resolve =>
      setTimeout(() => {
        if (res.borderHitWrappers && res.borderHitWrappers.length) {
          hasHitReports = true;
          crossingContext._setState({
            borderHitWrappers: res.borderHitWrappers
          });
          history.push(routePaths.crossing.hitReport);
        } else {
          resolve(hasHitReports);
        }
      }, 1000)
    );
  };

  updateCrossing = async () => {
    const {
      apiService: { post },
      crossingContext,
      history
    } = this.props;

    const update = {
      citizenEntryBasicDetailsForm: {
        travelerId: crossingContext.personId,
        travelPassNumber: crossingContext.travelPassNumber,
        travelPassReason: crossingContext.travelPassReason,
        dependents: crossingContext.dependents
          .filter(d => d.active)
          .map(d => ({ dependentId: d.personId }))
      }
    };

    await post({
      method: "PUT",
      url: makeCrossingUrlFor(
        UPDATE,
        crossingContext.travelerType,
        crossingContext.crossingType
      ),
      data: update
    });
  };

  completeCrossing = async () => {
    const {
      apiService: { post },
      crossingContext
    } = this.props;
    const body = {
      crossingDetailsBasicForm: {
        travelerId: crossingContext.personId,
        crossingStatus: "cross",
        clear: true
      }
    };
    await post({
      method: "PUT",
      url: makeCrossingUrlFor(
        COMPLETE,
        crossingContext.travelerType,
        crossingContext.crossingType
      ),
      data: body
    });
  };

  onChange = target => {
    target.name === "isSkipBiometricVerification" &&
      this.setState({
        [target.name]: target.checked
      });
  };

  toggleDependent = personId => {
    let dependents = this.props.crossingContext.dependents.map(d => ({
      ...d,
      active: personId === d.personId ? !d.active : d.active
    }));
    this.props.crossingContext._setState(state => ({
      dependents,
      isDependentsConfirmed: state.isDependentsConfirmed
        ? false
        : state.isDependentsConfirmed
    }));
  };

  selectedDependents = () => {
    const result = this.props.crossingContext.dependents.filter(d => d.active);
    if (result.length > 0) return result;
    return null;
  };

  confirmDependents = () => {
    this.props.crossingContext._setState({
      isDependentsConfirmed: true
    });
  };

  render() {
    const {
      dependents,
      travelPermissionInfo,
      travelerType,
      crossingType,
      isDependentsConfirmed
    } = this.props.crossingContext;
    console.log("------dependents:", dependents);
    const { hasRoles } = this.props.sessionContext;
    const { currentState } = this.state;
    const modalMessage = ((currentState === "loading"
      ? intl.get("searchingMsg")
      : currentState === "success"
        ? crossingType === ARRIVAL
          ? intl.get("entryClearedMsg")
          : intl.get("exitClearedMsg")
        : ""): "");
    const selectedDependents = this.selectedDependents();
    const title = `${intl.get(crossingType || ARRIVAL)} ${intl.get(
      travelerType
    )}`;
    return (
      <PageContainer title={title}>
        <div id="search-result">
          <Columns>
            <Column className="is-narrow" style={{ maxWidth: "224px" }}>
              <Image src={profileImage} />
            </Column>
            <Column>
              <Form ref={this.formEl}>
                {travelerType === CITIZEN && <CitizenFields />}
                {travelerType === ALIEN && <AlienFields />}
                {(travelerType === VISITOR || travelerType === PILGRIM) && (
                  <VisitorFields />
                )}
              </Form>
              <br />
              <Collapse
                isCollapsed={dependents.length === 0}
                label={intl.get("dependentList")}
              >
                <div className="dependents-list-table-container">
                  <Table
                    className="is-fullwidth is-striped is-hoverable"
                    thead={
                      <tr>
                        {dependentListHeaders.map(h => (
                          <th key={h}>{intl.get(h).d(h)}</th>
                        ))}
                      </tr>
                    }
                    tbody={dependents.map(d => {
                      return (
                        <tr key={d.personId}>
                          {dependentListHeaders.map(header => {
                            if (header === "include") {
                              return (
                                <td key={header}>
                                  <Checkbox
                                    checked={d.active}
                                    onChange={() =>
                                      this.toggleDependent(d.personId)
                                    }
                                  />
                                </td>
                              );
                            } else if (header === "id") {
                              return <td key={header}>{d["personId"]}</td>;
                            } else {
                              return <td key={header}>{d[header]}</td>;
                            }
                          })}
                        </tr>
                      );
                    })}
                  />
                </div>
                {selectedDependents && (
                  <div className="dependent-confirm-container">
                    {selectedDependents.length.toString() +
                      " " +
                      intl.get("dependentsSelected").d("dependentsSelected")}
                    &nbsp;&nbsp;
                    <Button
                      disabled={isDependentsConfirmed}
                      className="is-small is-primary"
                      onClick={this.confirmDependents}
                    >
                      {isDependentsConfirmed ? (
                        <span>
                          {intl.get("Confirmed".d("Confirmed"))}
                          &nbsp;
                          <Icon icon="fa fa-check" />
                        </span>
                      ) : (
                        intl.get("Confirm".d("Confirm"))
                      )}
                    </Button>
                  </div>
                )}
              </Collapse>
              <br />
              {travelerType === CITIZEN && (
                <Collapse
                  label={intl.get("travelPermissionInfo")}
                  isCollapsed={!travelPermissionInfo}
                >
                  <Textarea
                    rows="1"
                    disabled
                    value={travelPermissionInfo}
                    name="travelPermissionInfo"
                    style={{ resize: "none" }}
                  />
                </Collapse>
              )}
              <br />
              <Columns>
                <Column className="is-narrow">
                  <Checkbox label={intl.get("printCrossDocument")} />
                </Column>
                {hasRoles("RBC.SKIP.BIOMETRICS.VERIFY") && (
                  <Column>
                    <Checkbox
                      name="isSkipBiometricVerification"
                      onChange={this.onChange}
                      label={intl.get("skipBiometricVerification")}
                    />
                  </Column>
                )}
              </Columns>
              <br />
              <br />
              <br />
              <div className="buttons" style={{ justifyContent: "flex-end" }}>
                <Link
                  className="button has-shadow"
                  to={routePaths.crossing.tripInfo}
                >
                  {intl.get("backBtn")}
                </Link>
                <Button
                  className="is-primary has-shadow"
                  onClick={this.next}
                  disabled={selectedDependents && !isDependentsConfirmed}
                >
                  {intl.get("okBtn")}
                </Button>
              </div>
            </Column>
          </Columns>
        </div>
        {(currentState === "loading" || currentState === "success") && (
          <Modal
            hideModal={this.hideModal}
            content={
              <Box
                style={{
                  minHeight: "175px",
                  display: "flex",
                  justifyContent: "center",
                  flexDirection: "column"
                }}
              >
                {currentState === "loading" && (
                  <React.Fragment>
                    <div style={{ textAlign: "center" }}>
                      <Icon icon="fa fa-spinner fa-2x fa-pulse" />
                    </div>
                    <br />
                  </React.Fragment>
                )}
                <div style={{ textAlign: "center" }}>{modalMessage}</div>
              </Box>
            }
          />
        )}
      </PageContainer>
    );
  }
}

export default compose(
  withApiService,
  withSessionContext,
  withCrossingContext
)(SearchResult);



