import React from "react";
import intl from "react-intl-universal";
import TextField from "components/TextField";
import Label from "components/Label";
import Columns from "components/Columns";
import Column from "components/Column";
import SubTitle from "components/SubTitle";
import Checkbox from "components/Checkbox";
import Select from "components/Select";
import withCrossingContext from "HOC/withCrossingContext";

const AlienFields = () => (
  <React.Fragment>
    <Columns>
      <Column>
        <TextField
          disabled
          name="travelerId"
          label={<Label>{intl.get("travelerId")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="moiOrderNumber"
          label={<Label>{intl.get("moiOrderNumber")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="omraPermit"
          label={<Label>{intl.get("omraPermit")}</Label>}
        />
      </Column>
      <Column>
        <Select
          options={[{ value: "normal", displayName: "normal" }]}
          name="visitorType"
          label={<Label>{intl.get("visitorType")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <TextField
          disabled
          name="firstName"
          label={<Label>{intl.get("firstName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="fatherName"
          label={<Label>{intl.get("fatherName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="grandfatherName"
          label={<Label>{intl.get("grandfatherName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="familyName"
          label={<Label>{intl.get("familyName")}</Label>}
        />
      </Column>
    </Columns>
    <div>
      <SubTitle
        is="6"
        style={{
          textAlign: "left",
          borderBottom: "1px solid rgba(255, 255, 255, 0.5)",
          marginBottom: "5px"
        }}
      >
        {intl.get("translatedName")}
      </SubTitle>
    </div>
    <Columns>
      <Column>
        <TextField
          disabled
          name="translatedFirstName"
          label={<Label>{intl.get("translatedFirstName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="translatedFatherName"
          label={<Label>{intl.get("translatedFatherName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="translatedGrandFatherName"
          label={<Label>{intl.get("translatedGrandFatherName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="translatedFamilyName"
          label={<Label>{intl.get("translatedFamilyName")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <TextField
          disabled
          name="birthDate"
          label={<Label>{intl.get("birthDate")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="gender"
          label={<Label>{intl.get("gender")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="status"
          label={<Label>{intl.get("status")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <TextField
          disabled
          name="documentNumber"
          label={<Label>{intl.get("documentNumber")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="documentExpiryDate"
          label={<Label>{intl.get("documentExpiryDate")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="nationalityCode"
          label={<Label>{intl.get("nationalityCode")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="nationalityName"
          label={<Label>{intl.get("nationalityName")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <TextField
          disabled
          name="visaNumber"
          label={<Label>{intl.get("visaNumber")}</Label>}
        />
      </Column>
      <Column
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <Checkbox name="isIssueNewVisa" label={intl.get("isIssueNewVisa")} />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaTypeNumber"
          label={<Label>{intl.get("visaTypeNumber")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaTypeName"
          label={<Label>{intl.get("visaTypeName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaIssueDate"
          label={<Label>{intl.get("visaIssueDate")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaPayment"
          label={<Label>{intl.get("visaPayment")}</Label>}
        />
      </Column>
    </Columns>

    <Columns>
      <Column>
        <TextField
          disabled
          name="visaStayingDays"
          label={<Label>{intl.get("visaStayingDays")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaCountryCode"
          label={<Label>{intl.get("visaCountryCode")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaCountryName"
          label={<Label>{intl.get("visaCountryName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaValidityDays"
          label={<Label>{intl.get("visaValidityDays")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaAmountToP"
          label={<Label>{intl.get("visaAmountToP")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaComment"
          label={<Label>{intl.get("visaComment")}</Label>}
        />
      </Column>
    </Columns>

    <Columns>
      <Column>
        <TextField
          disabled
          name="visaIssuePlaceNumber"
          label={<Label>{intl.get("visaIssuePlaceNumber")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaIssuePlaceName"
          label={<Label>{intl.get("visaIssuePlaceName")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaSponsorNationality"
          label={<Label>{intl.get("visaSponsorNationality")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="visaSponsorFullName"
          label={<Label>{intl.get("visaSponsorFullName")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <TextField
          disabled
          name="exitEntryTravelDate"
          label={<Label>{intl.get("exitEntryTravelDate")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="exitEntryCountryCode"
          label={<Label>{intl.get("exitEntryCountryCode")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="exitEntryCountryName"
          label={<Label>{intl.get("exitEntryCountryName")}</Label>}
        />
      </Column>
    </Columns>
    <Columns>
      <Column>
        <Select
          options={[
            { value: "journalist", displayName: "journalist" },
            { value: "painter", displayName: "painter" },
            { value: "artist", displayName: "artist" }
          ]}
          name="exitEntryVisitorDescription"
          label={<Label>{intl.get("exitEntryVisitorDescription")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="exitEntryCarrierNumber"
          label={<Label>{intl.get("exitEntryCarrierNumber")}</Label>}
        />
      </Column>
      <Column>
        <TextField
          disabled
          name="exitEntryCarrierName"
          label={<Label>{intl.get("exitEntryCarrierName")}</Label>}
        />
      </Column>
    </Columns>
  </React.Fragment>
);

export default withCrossingContext(AlienFields);



